.container {
  display: flex;
  flex-direction: column;
  gap: 5rem;
  padding: 6rem;
}

.form {
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
}

.field {
  display: flex;
  flex-direction: column;
  gap: .8rem;

  label {
    max-width: 40rem;
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: .8rem;
  }
}

.btns_panel {
  margin-top: 2rem;
  display: flex;
  flex-direction: row;
  gap: 2rem;
  justify-content: space-between;
}

label.textarea {
  max-width: 100%;
  width: 100%;
}

.info {
  display: flex;
  flex-direction: row;
  gap: 2rem;
  justify-content: space-between;

  & .counter {
    align-self: flex-end;
    margin-left: auto;
  }
}



@media screen and (max-width: 600px) {
  .container {
    padding: 1rem;
  }
}